@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
    margin: 0;
}
#topbar{
    display: -webkit-flex;
    display: flex;
    padding: 15px 6%;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    background-color: white;
    box-shadow: 2px 4px 2px rgb(17 16 16 / 50%);
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 10;
}
a{
    text-decoration: none;
    color: black;
}
#logo{
    text-transform: uppercase;
    font-size: 30px;
    font-weight: 400;
    letter-spacing: 2px;
}
#logo>span{
    font-weight: 600;
    color: #009688;
}
#nav-wrapper{
    padding-top: 10px;
}
.nav-link{
    text-transform: uppercase;
    margin: 0 40px;
    font-weight: 500;
}
.nav-link:hover{
    color: #009688;
}
#icon-wrapper{
    display: -webkit-flex;
    display: flex;
    padding: 10px 30px 0;
}
svg{
    cursor: pointer;
}
.burger-toggle{
    cursor: pointer;
    margin-left: 20px;
    display: none;
}
.burger-toggle div{
    width: 25px;
    height: 3px;
    background-color: rgb(27, 25, 25);
    margin: 5px;
    transition: all 0.3s ease;
}
.line1{
    -webkit-transform: rotate(-45deg) translate(-5px, 6px);
            transform: rotate(-45deg) translate(-5px, 6px);
}
.line2{
    opacity: 0;
}
.line3{
    -webkit-transform: rotate(45deg) translate(-5px, -6px);
            transform: rotate(45deg) translate(-5px, -6px);
}
#cart{
    display: inline-block;
    margin: 0 30px;
    position: relative;
}
#cart>span{
    display: inline-block;
    color: #fff;
    background-color: #009688;
    border-radius: 50%;
    font-size: 12px;
    padding: 3px 6px;
    position: absolute;
    top: -10px;
    right: -10px;
}
#nav-links{
    position: absolute;
    right: 0;
    height: 100vh;
    top: 10.4vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    width: 50%;
    transition: 1s ease-in;
    background: #009699;
    z-index: 100;
}
#nav-links a{
    margin-top: 100px;
}

@media(max-width:1200px){
    #nav-wrapper{
        display: none;
    }
    .burger-toggle{
        display: block;
    }
}
@media(max-width:460px){
    #logo{
        font-size: 24px;
    }
}
@media(max-width:400px){
    #logo{
        font-size: 20px;
    }
    .burger-toggle{
        margin-left: 10px;
    }
    #cart{
        margin: 0 15px;
    }
}
#footer{
    text-transform: uppercase;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    padding: 30px;
    background-color: black;
    color: white;
}
#footer>div{
    padding: 5px 20px 10px;
}
#footer a{
    display: block;
    color: #b8b8b8;
    font-size: 12px;
    margin-bottom: 5px;
}
#footer a:hover{
    color: #009688;
}
#footer h3{
    margin-bottom: 8px;
    font-weight: 400;
    font-size: 16px;
}
@media(max-width:600px){
    #footer{
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
    }
    #footer>div{
        width: 50%;
        margin-bottom: 30px;
    }
}
@media(max-width: 375px){
    #footer>div{
        width:80%;
    }
}
#features-wrapper{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin-top: 70px;
}
.feature{
    width: 25%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    margin-bottom: 20px;
}
.image-wrapper{
    width: 90px;
    height: 90px;
    border-radius: 50%;
    background-color: #009688;
    padding: 15px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-animation: spreading 3s linear infinite ;
            animation: spreading 3s linear infinite ;
    cursor: pointer;
    margin: 20px 0;
}
.image-wrapper>img{
    width: 40px;
    height: 38px;
}
.feature>h4{
    font-weight: 400;
}
@-webkit-keyframes spreading{
    0%{
        box-shadow: 0 0 0 0 rgb(0 150 136 / 70%), 0 0 0 0 rgb(0 150 136 / 70%);
    }
    40%{
        box-shadow: 0 0 0 50px rgb(0 150 136 / 0%), 0 0 0 0 rgb(0 150 136 / 70%);
    }
    80%{
        box-shadow: 0 0 0 50px rgb(0 150 136 / 0%),0 0 0 30px rgb(0 150 136 / 0%);
    }
    100%{
        box-shadow: 0 0 0 0 rgb(0 150 136 / 0%), 0 0 0 30px rgb(0 150 136 / 0%);
    }
}
@keyframes spreading{
    0%{
        box-shadow: 0 0 0 0 rgb(0 150 136 / 70%), 0 0 0 0 rgb(0 150 136 / 70%);
    }
    40%{
        box-shadow: 0 0 0 50px rgb(0 150 136 / 0%), 0 0 0 0 rgb(0 150 136 / 70%);
    }
    80%{
        box-shadow: 0 0 0 50px rgb(0 150 136 / 0%),0 0 0 30px rgb(0 150 136 / 0%);
    }
    100%{
        box-shadow: 0 0 0 0 rgb(0 150 136 / 0%), 0 0 0 30px rgb(0 150 136 / 0%);
    }
}

@media(max-width:425px){
    #features-wrapper{
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
    }
    .feature{
        width: 50%;
        height: auto;
    }
}
#product-container{
    padding: 3% 6%;
}
#product-container h1{
    font-weight: 500;
    font-size: 26px;
}
.products-section{
    margin: 50px auto;
}
.products{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 0 4%;
}
.card{
    color: black;
    width: 18%;
    margin: 15px 1%;
    box-shadow: 0 2px 10px #ccc;
}
.card:hover{
    box-shadow: 0 2px 10px #999;
}
.productImage{
    width: 100%;
    cursor: pointer;
}
.productInfo{
    padding: 10px;
    height: 160px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.productName{
    font-size: 15px;
    font-weight: 500;
}
.productBrand{
    font-size: 12px;
    color: grey;
    font-weight: 500;
    margin: 6px 0;
}
.productPrice{
    font-size: 14px;
    font-weight: 700;
    margin: 0;
    color: lightseagreen;
}
.addToCart{
    padding: 4px 12px;
    outline: none;
    border: none;
    color: white;
    background-color: lightseagreen;
    border-radius: 4px;
    cursor: pointer;
    box-shadow: 0 2px 10px rgb(0 0 0 / 50%);
}
.addToCart:hover{
    -webkit-transform: scale(1.02,1.02);
            transform: scale(1.02,1.02);
    box-shadow: 0 2px 10px rgb(0 0 0 / 80%);
}


@media(max-width:991px){
    .products{
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
    }
    .card{
        width: 20%;
    }
    .productName{
        font-size: 12px;
    }
    .productBrand{
        font-size: 9px;
    }
    .productPrice{
        font-size: 11px;
    }
    .productInfo{
        height: 140px;
    }
}

@media(max-width:768px){
    .card{
        width: 30%;
    }
    #product-container h1{
        font-size: 22px;
    }
}

@media(max-width:540px){
    .card{
        width: 45%;
    }
}
@media(max-width:410px){
    .card{
        width: 80%;
    }
    .productName{
        font-size: 15px;
    }
    .productBrand{
        font-size: 12px;
    }
    .productPrice{
        font-size: 14px;
    }
}
.slick-slider>button{
  display: none !important;
}

/* Slideshow container */
.slideshow-container {
  width: 100%;
}

/* Hide the images by default */
.mySlides {
  display: none;
}

/* The dots/bullets/indicators */
.dot {
  cursor: pointer;
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.active, .dot:hover {
  background-color: #717171;
}

/* Fading animation */
.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}

@-webkit-keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}

@keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}
